body.dark-theme {
  background-color: #272424;
  color: var(--header-color-text);
 & footer{
  background-color: #272424;
  border-top: 1px solid var(--params-name);
  color: var(--header-color-text);
}
& .modal{
  background-color: #333;
 
}
& .modal__close-btn{
  
  &:before,
  &:after {
      background-color: #fff;
      }
    &:hover,
    &:focus{
      border-radius: 50%;
      border-top: 1px solid var(--modal-background-color);
      border-bottom: 1px solid var(--modal-background-color);
      
    }
} 
& .stats__value{
  color: var(--header-color-text);
}
& .stats__votes{
  color: var(--color-title);
  background-color: var(--params-name);
}
& .movie__button{
  border: 1px solid var(--modal-background-color);
  color: var(--header-color-text);
}
& .movie__button-trailer{
  color: var(--header-color-text);
}
& .reg-modal,
& .singIn-modal{
  background-color: #333;
}
& .reg-modal-email,
& .reg-modal-password,
& .singIn-modal-password,
& .singIn-modal-email{
  border: 1px solid var(--modal-background-color);
  background-color: transparent;
}
 & .modal-wrap-bottom{
  color: var(--header-color-text);
 }

  & .card__title {
    color: #cccccc;
  }


  .card__link:hover .card__img {
    box-shadow: 0 1px 4px rgba(119, 117, 117, 0.3),
      -23px 0 20px -23px rgba(90, 88, 88, 0.6), 23px 0 20px -23px rgba(224, 219, 219, 0.6),
      inset 0 0 40px rgba(235, 230, 230, 0.1);
  }
}




.toolbar {
  display: flex;
  justify-content: flex-end;
  
}
.theme-switch {
  display: flex;
  align-items: center;
}

.theme-switch__icon {
  color: lightgrey;
  height: 15px;
  width: 15px;
  margin-right: 8px;
  margin-left: 8px;
}

.theme-switch__control {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  position: relative;
  width: 32px;
  height: 16px;
  border-radius: 50em;
  padding: 3px 0;
}

.theme-switch__toggle {
  position: absolute;
  left: 0;
  top: 0;
  margin: 0;
  padding: 0;
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
}

.theme-switch__track {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  color: transparent;
  user-select: none;
  background-color: #333;
  border-radius: inherit;
  z-index: 1;
  cursor: pointer;
}

.theme-switch__marker {
  position: relative;
  background-color: #fff;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  z-index: 2;
  pointer-events: none;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25);
  transform: translateX(3px);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.theme-switch__toggle:checked + .theme-switch__track {
  background-color: var(--accent);
}

.theme-switch__toggle:checked ~ .theme__marker {
  transform: translateX(17px);
}

.theme-switch__toggle:focus + .theme-switch__track,
.theme-switch__toggle:active + .theme-switch__track {
  box-shadow: 0 0 0 3px var(--accent);
}
